.main-nav {
  display: grid;
  grid-template-columns: 2fr 5fr 1fr 1fr;
  row-gap: 20px;
}

.main-nav__logo {
  grid-column: 2 / 3;
  margin: 0 auto;
}

.main-nav__logo-icon {
  fill: #a87b62;
}

.main-nav__form {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  margin-top: 10px;
}

.main-nav__login {
  grid-column: 3 / 4;
  margin-top: 10px;
}

.main-nav__cart {
  grid-column: 4 / -1;
  margin-top: 10px;
}

.main-nav__cart-icon {
  display: none;
  fill: transparent;
  stroke: #1d1613;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  grid-column: 1 / -1;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  margin-right: 35px;

  &:last-child {
    margin-right: 0;
  }
}

.main-nav__link {
  padding-top: 3px;
  padding-left: 5px;
  padding-bottom: 3px;
  padding-right: 5px;
}

.main-nav__sublist {
  display: none;
}

@media (max-width: $tablet-width) {
  .main-nav--closed .main-nav__form,
  .main-nav--closed .main-nav__login,
  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav {
    position: relative;
    margin-bottom: 0;
    max-width: 100%;

    &--nojs {
      position: relative;
      margin-top: 101px;
      margin-bottom: 119px;

      .main-nav__link {
        padding-left: 0;

        &::before,
        &::after {
          right: 9px;
        }
      }
    }

    &--nojs .burger {
      display: none;
    }

    &--nojs .main-nav__list {
      position: static;
      display: block;
    }

    &--closed .burger {
      &::before {
        content: "";
        position: absolute;
        width: 25px;
        height: 2px;
        background-color: #000000;
        box-shadow: 0 -7px 0 0 #000000, 0 7px 0 0 #000000;
        transform: translateX(-10px);
      }
    }

    &--opened {
      grid-template-rows: min-content min-content 1fr 1fr;
      position: absolute;
      margin: 0;
      padding-top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
      background-color: #ffffff;

      .main-nav__logo-icon {
        fill: #ffffff;
      }

      .main-nav__list {
        grid-row: 3 / 4;
        display: block;
        margin: 0;
        width: 100%;
      }

      .main-nav__form {
        grid-row: 2 / 3;
        grid-column: 1 / -1;
        margin-top: 10px;
        margin-left: 45px;
        margin-right: 45px;
      }

      .main-nav__form input {
        width: 100%;
      }

      .main-nav__login {
        grid-row: 4 / -1;
        grid-column: 1 / -1;
        margin-top: 10px;
        margin-left: 45px;
      }
    }

    &--opened .burger {
      &::before {
        content: "";
        position: absolute;
        width: 25px;
        height: 2px;
        background-color: red;
        box-shadow: 0 -7px 0 0 red, 0 7px 0 0 red;
        transform: translateX(-10px);
      }
    }
  }

  .main-nav__item {
    position: relative;
    margin-right: 0;
    margin-bottom: 30px;

    &::after {
      position: absolute;
      content: "";
      top: -177px;
      left: -485px;
      width: 1520px;
      height: 240px;
      background-color: #a87b62;
      z-index: -10;
    }
  }

  .main-nav__link {
    position: relative;
    font-size: 20px;
    font-weight: 400;
    padding-left: 45px;
    padding-top: 13px;
    padding-right: 75%;
    padding-bottom: 13px;
    color: #48494d;
    opacity: 1;

    &:hover {
      opacity: 0.7;
    }
  }

  .main-nav__cart-text {
    display: none;
  }

  .main-nav__cart-icon {
    display: block;
  }
}